<template>
  <v-card>
    <mrc_ess_iconGroup :is_disabled='readonly'
                       :showReturn='false'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>

    <mrc_ess_switch v-if="$store.state.uuid"
                    :true-value="1"
                    :false-value="0"
                    :readonly='readonly'
                    v-model="list.status"
                    :true-text="$t('label_To_enable_the')"
                    :false-text="$t('label_disable')"></mrc_ess_switch>
    <v-row>
      <v-col cols="6">
        <v-text-field v-model="list.language_master_key"
                      :readonly='readonly'
                      :error-messages="$errors('list','language_master_key',requiredRule.concat(nocharacter))"
                      label="Key*"></v-text-field>
      </v-col>
      <v-col cols="6">
        <!-- 版本号 -->
        <v-text-field v-model="list.version_uuid"
                      :readonly='true'
                      :label="$t('label_version')+'*'"></v-text-field>
      </v-col>
      <v-col cols="6">
        <!-- <v-text-field v-model="list.language_master_module_uuid"
                      :readonly='readonly'
                      label="模块"></v-text-field> -->
        <mrc_ess_prompt v-model="list.language_master_module_uuid"
                        :obj='module_reference'
                        :label="$t('label_The_module')"
                        chips
                        :rules='false'
                        :disabled='readonly'>
        </mrc_ess_prompt>
      </v-col>
      <v-col cols="6">
        <!-- <v-text-field v-model="list.language_master_function_uuid"
                      :readonly='readonly'
                      label="功能"></v-text-field> -->
        <mrc_ess_prompt v-model="list.language_master_function_uuid"
                        :obj='function_reference'
                        :label="$t('label_function')"
                        chips
                        :rules='false'
                        :disabled='readonly'>
        </mrc_ess_prompt>
        <!-- <mrc_ess_dictName label="功能"
                          chips
                          :rules='false'
                          :disabled='readonly'
                          v-model="list.language_master_function_uuid"
                          :dict_name='module_reference'></mrc_ess_dictName> -->

      </v-col>
      <v-col cols="12">
        <v-text-field v-model="list.language_master_content"
                      :readonly='readonly'
                      :error-messages="$errors('list','language_master_content',requiredRule.concat(nocharacter).concat(maxLength('200')))"
                      :label="$t('label_content')+'*'"></v-text-field>
      </v-col>
      <v-col cols="12"
             v-if="$store.state.uuid">
        <v-text-field v-model="list.language_master_final_key"
                      :readonly='true'
                      :label="$t('label_Final_code')+'*'"></v-text-field>
      </v-col>
      <!-- <v-col cols="12"
             v-else>
        <v-text-field v-model="view_language_master_final_key"
                      :readonly='true'
                      label="最终编码*"></v-text-field>
      </v-col> -->
      <mrc_ess_submitBtn @cancel='cancel'
                         :loading='btnIsLoading'
                         @success='success'
                         v-if="!readonly"></mrc_ess_submitBtn>
    </v-row>

  </v-card>
</template>

<script>
import { gethintList, gethintDetail, deletehint, addehint } from '@/service/api/internationalization.js'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {

  },
  props: {
  },
  data () {
    return {
      btnIsLoading: false,
      readonly: true,//默认只读
      list: {},
      item: 1
    };
  },
  created () {
  },
  async mounted () {
    if (this.$store.state.uuid) {
      await this.getdetail()
      this.readonly = this.$store.state.iconstatus
      this.$route.meta.title = this.$route.params.title
    } else {
      this.list.status = 1
      this.readonly = false
    }
  },
  methods: {
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)
    },
    handleDel () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_deletion')
      }).then(res => {
        deletehint({
          uuid: this.$store.state.uuid
        }).then(res => {
          this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
          this.$router.push({
            name: 'labelmanagement'
          })
        })
      }).catch(err => { })
    },
    cancel () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_cancellation_of_the_edit')
      }).then(res => {
        this.$router.push({
          name: 'labelmanagement'
        })
      }).catch(err => { })
    },
    success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      this.btnIsLoading = true
      if (this.$store.state.uuid) {
        let list = this.$deletefield(null, this.list)
        delete list.language_master_final_key
        delete list.language_master_id
        // delete list.language_master_error_reason_method
        addehint({ ...list, language_data_type: 'label' }).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.$router.push({
              name: 'labelmanagement'
            })
          }
          else {
            this.$store.commit('newsTips', res.msg)
            this.btnIsLoading = false
          }
        })
      }
      else {
        delete this.list.language_master_final_key
        addehint({ ...this.list, language_data_type: 'label' }).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.$router.push({
              name: 'labelmanagement'
            })
          }
          else {
            this.$store.commit('newsTips', res.msg)
            this.btnIsLoading = false
          }
        })
      }
    },
    async getdetail () {
      const uuid = this.$store.state.uuid
      const res = await gethintDetail({ uuid })
      if (res.code === '200') {
        this.list = res.data
      }
    }
  },
  computed: {
    // view_language_master_final_key () {
    //   return 'label' + '_' + this.list.language_master_module_uuid + '_' + this.list.language_master_function_uuid + '_nodata'
    // }
    module_reference () {
      return { prompt_name: 'module_reference', uuid: this.$store.state.currentCompany }
    },
    function_reference () {
      return { prompt_name: 'function_reference', uuid: this.list.language_master_module_uuid }
    }
  },
  watch: {

  },
  validations: {
    list: {
      language_master_key: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      language_master_content: {
        required,
        maxLength: maxLength(200),
        nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
    }
  },
};
</script>

<style scoped>
.cardbox {
  padding: 15px 30px;
}
</style>
